.profile__title {
  margin: 0;
  max-width: 373px;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 740px) {
  .profile__title {
    font-size: 22px;
    line-height: 27px;
  }
}
